.contentBase {
  margin: 5rem auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  .profile {
    > img {
      width: 10rem;
      height: 10rem;
      border-radius: 50%;
    }
  }

  .links {
    margin-top: 2rem;
    ul {
      list-style-type: none;
    }
    > ul {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
    }
    li > ul {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
    }
    > ul > li {
      cursor: pointer;
    }
  }
}
